*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
html{
    font-size: 62.5%;
}
body{
    font-family: 'roboto-regular';
}

.header{
    height: 100vh;
    background-size: cover;
    font-family: 'PoetsenOne';
    background-image: url(/movies/static/media/header.eb09afa1.jpg);
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    background-attachment: fixed;
}

.heading-primary{
    background-color: rgba(255,255, 255, 0.5);
    padding: 2rem 4rem;
    border-radius: 0.5rem;
    text-align: center;
}
.heading-primary--main{
    text-transform: uppercase;
    letter-spacing: 0.1rem;
    font-size: 3.5rem;
}
.heading-primary--sub{
    color: #6610f2;
    font-size: 2.5rem;
    margin: 1rem;
    text-transform: capitalize;
}
@media (min-width:600px){
    .heading-primary--main{
        font-size: 6rem;
    }
    .heading-primary--sub{
        font-size: 4rem;
    }
}
.nav-bar{
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 2;
}
.nav__list{
    list-style: none;
    display: -webkit-flex;
    display: flex;
    font-size: 1.7rem;
    padding: 1rem 0;
    background-color: #d4d4d4;
    font-weight: 700;
   
}
.nav__link{
    text-decoration: none;
    text-transform: uppercase;
    padding: 0 3rem;
    color: #000;
}
.active{
    color: #6610f2;
}
.loading-container{
    margin: 5rem auto;
    max-width: 80%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-direction: column;
            flex-direction: column;
}
.loading__text{
    font-size: 3rem;
