.add-section{
    background-color: #333;
    text-align: center;
    padding: 1rem;
   
    margin-top: 5rem;
}
.add-container{
    background-color: rgba(255, 255, 255, 0.4);
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: 2rem auto 3rem;
    padding: 2rem 4rem;
    border-radius: 0.5rem;
}
.add-title{
    display: inline-block;
    text-transform: uppercase;
    letter-spacing: 0.1rem;
    color: #9C27B0;
    font-size: 1.8rem;
}
.btn-add{
    padding: 1rem 2rem;
    margin-left: 2rem;
    background-color: #fff;
    transition: 0.3s;
}
.btn-add:hover{
    background-color: #9C27B0;
    color: #fff;
}
.success-msg{
    text-align: center;
    margin-top: 2rem;
    font-size: 2rem;
    color: green;
    transition: 0.3s;
}
@media (min-width:600px){
    .add-section{
        -webkit-clip-path: polygon(5% 0, 100% 0, 100% 75%, 95% 100%,0 100%, 0 25%);
                clip-path: polygon(5% 0, 100% 0, 100% 75%, 95% 100%,0 100%, 0 25%);
    }
}
.btn{
    border: 1px solid black;
    border-radius: 1rem;
    display: inline-block;
    cursor: pointer;
    font-size: 1.5rem;
}
.btn:hover{
}
.movies-section{
    max-width: 90%;
    margin: 0 auto;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
}

.movies-title{
    text-align: center;
    text-transform: uppercase;
    font-size: 2rem;
    margin: 5rem 2rem 1rem;
    letter-spacing: 0.1rem;
}
.movies-container{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    padding: 2rem;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: flex-start;
            align-items: flex-start;
}
@media (min-width:600px){
    .movies-section{
        max-width: 80%;
    }
}
.card{
    width: 20rem;
    margin: 2rem;
    border-radius: 0.3rem;
    box-shadow: 0 1.5rem 4rem rgba(0,0,0,0.2);
    transition: 0.3s;
}
.card:hover .card-poster{
    -webkit-filter: opacity(50%);
            filter: opacity(50%);
}
.card__top{
    position: relative;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
}
.card-poster{
    width: 100%;
    height: 25rem;
    object-fit: cover;
    border-radius: 0.3rem;
    display: block;
    transition: 0.3s;
}

.btn-card{
    position: absolute;
    top: 1rem;
    padding: 0.5rem 1rem;;
    background-color: #fff;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    transition: 0.3s;
}
.btn-card:hover{
    background-color: blueviolet;
    color: #fff;
}
.btn-left{
    left: 0.5rem;
}
.btn-right{
    right: 0.5rem;
}
.card__bottom{
    border: 1px solid black;
    border-top: none;
    margin-top: -0.4rem;
    border-radius: 0.3rem;
    padding: 1.2rem 0.6rem 0.8rem;
    font-size: 1.3rem;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
}
.movie-title{
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    font-size: 1.5rem;
}
.movie-sub-details{
    margin-top: 0.5rem;
}
.modal-content{
    padding: 2rem;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
}
.modal-title{
    text-align: center;
    font-size: 2rem;
    margin-bottom: 1rem;
    padding-bottom: 0.5rem;
    border-bottom: 0.2rem solid #4CAF50;
    text-transform: uppercase;
    border-radius: 0.5rem;
}
.modal-actions{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin-top: 1rem;
}
.btn-cancel,.btn-apply{
    padding: 0.5rem 1rem;
    transition: 0.3s;
    background-color: #fff;
}
.btn-cancel{
    margin-right: 1rem;
}
.btn-apply{
    margin-left: 1rem;
}
.btn-cancel:hover{
    color: #fff;
    background-color: red;
}
.btn-apply:hover{
    color:#fff;
    background-color: #4CAF50;
}

.form-input{
    border: none;
    border-bottom: 1px solid black;
    outline: none;
    padding-left: 0.5rem;
    background-color: inherit;
}
.form-item{
    padding: 1rem;
    display: -webkit-flex;
    display: flex;
}
.form-label{
    font-size: 1.5rem;
    margin-right: 0.5rem;
}
.error-msg{
    color: #ff0000;
    margin-top: 0.5rem;
}
.input-container{
    display: inline-block;
}
.hidden{
    display: none;
}
.about-section{
    background-color: #f6f6f6;
    padding: 1px 0 5rem;
}
.about-container{
    text-align: center;
    width: 90%;
    margin: 5rem auto 0;
}
.about-content{
    background-color: #d6d6d6;
    padding: 2rem;
    border: 0.1rem solid black;
    border-radius: 0.4rem;
}
.about-title{
    font-size: 3rem;
    text-transform: uppercase;
    border-bottom: 0.2rem solid #4CAF50;
    border-radius: 0.5rem;
    padding: 0 1.5rem 1rem;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: 0 auto 2rem;
}
.about-body{
    font-size: 2rem;
    line-height: 3rem;
}
.about-p > a, .about-body > a {
    text-decoration: none;
    color: #4CAF50;
    transition: 0.3s;
}
.about-p{
    margin-top: 1rem;
}
@media (min-width:600px){
    .about-container{
        width: 60%;
    }
}

